import { http } from '@/http';
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { defineComponent, onMounted, ref } from 'vue';
export default defineComponent({
    name: 'MyPoints',
    props: {
        shopName: {
            type: String
        }
    },
    setup(props) {
        const loading = ref(true);
        const point = ref(null);
        const headimg = ref('');
        const myPoints = ref(null);
        const nickname = ref('');
        const getUserInfo = () => {
            http
                .get('/syx/user/info', {})
                .then((res) => {
                loading.value = false;
                if (res.code === '200') {
                    point.value = res.data.point;
                    headimg.value = res.data.user.headimg;
                    if (props.shopName) {
                        nickname.value = props.shopName;
                    }
                    else {
                        nickname.value = res.data.user.nickname;
                    }
                }
                else {
                    Toast.fail(res.msg);
                }
            })
                .catch((err) => {
                console.log(err);
            });
        };
        const getPoints = () => {
            http
                .get('/syx/user/score/list', {})
                .then((res) => {
                if (res.code === '200') {
                    myPoints.value = res.data;
                }
                else {
                    Toast.fail(res.msg);
                }
            })
                .catch((err) => {
                console.log(err);
            });
        };
        onMounted(() => {
            getUserInfo();
            getPoints();
        });
        return {
            loading,
            myPoints,
            point,
            headimg,
            nickname
        };
    }
});
