import { http } from '@/http';
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { defineComponent, onMounted, ref } from 'vue';
export default defineComponent({
    name: 'MyGifts',
    setup() {
        const giftsList = ref(null);
        const getMyGiftsDetail = () => {
            const params = {
                recordId: 0,
                size: 10,
                actTypeMain: 2
            };
            http
                .post('/syx/awd/list/part', params, false)
                .then((res) => {
                if (res.code === '200') {
                    giftsList.value = res.data;
                }
                else {
                    Toast.fail(res.msg);
                }
            })
                .then((err) => {
                console.log(err);
            });
        };
        onMounted(() => {
            getMyGiftsDetail();
        });
        return {
            giftsList
        };
    }
});
